const app = getApp();
const common = require('../../publicjs/common.js');
// import uniIcons from '@/components/uni-icons/uni-icons.vue';
import uniDrawer from '@/components/uni-drawer/uni-drawer.vue';
import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
export default {
	name: 'uniSearchBar',
	components: {
		// uniIcons,
		uniDrawer,
		uniList,
		uniListItem
	},
	props: {
		searchIcon: { //搜索图标
			type: String, 
			default: 'https://cmrs-oos.oss-cn-beijing.aliyuncs.com/sys/icon_search.png' ,
		},
		inputVal: { 
			 //搜索默认值
			type: String, 
			value: '' ,
		}
	},
	data() {
		return {
			showRight: false,
			showLeft: false,
			avatar: 'https://cdn.jsdelivr.net/gh/foxhuli229/cdn@1.4/images/z_avater.png',
			nickname: '没有昵称咯',
			college: '暂未填写咯'
		};
	},
	mounted() {
		this.getuserinfo();
	},
	onShow() {
		console.log("onshow")
	},
	onNavigationBarButtonTap(e) {
		this.showRight = !this.showRight;
	},
	onBackPress() {
		if (this.showRight || this.showLeft) {
			this.hide();
			return true;
		}
	},
	methods: {
		//获取缓存中的系统信息
		getuserinfo() {
			try {
			    const value = uni.getStorageSync('userinfolist');
			    if (value) {
					this.avatar =  value.portrait;
					this.nickname =  value.nickname || "没有昵称咯";
					this.college = (value.college == null || value.college == "" ? "暂未填写咯" : value.college)
			    }
			} catch (e) {
			    // error
				console.log("获取失败")
			}
		},
		show(e) {
			console.log('show', e);
			if (e === 'left') {
				this.showLeft = true;
			} else {
				this.showRight = true;
			}
		},
		closeDrawer(e) {
			if (e === 'left') {
				this.showLeft = false;
			} else {
				this.showRight = false;
			}
		},
		//跳转我的用户主页
		gomyuser() {
			uni.navigateTo({
				url: '/pages/userCentre/index/index'
			})
		},
		//跳转到个人信息页面
		gomyuserinfo() {
			let param = {
				pagesurl: '/pages/userCentre/index/index'
			}
			param = JSON.stringify(param)
			uni.navigateTo({
				url: "/pages/userCentre/baseinfo/baseinfo?item=" + param
			})
		},
		//跳转到首页
		goindex() {
			uni.navigateTo({
				url: "/pages/home/index/index"
			})
		},
		//跳转到团队招募
		gotearm() {
			uni.navigateTo({
				url: "/pages/teamRecruitment/index/index"
			})
		},
		//跳转到悬赏
		goreward() {
			console.log("未填写跳转的url地址，请填写");
		},
		//跳转到活动
		goactity() {
			uni.navigateTo({
				url: "/pages/universityActivities/activityIndex/activityIndex"
			})
		},
		//跳转到通知
		gomyinform() {
			let param = {
				pagesurl: '/pages/userCentre/index/index'
			}
			param = JSON.stringify(param)
			uni.navigateTo({
				url: "/pages/userCentre/inform/inform?item=" + param
			})
		},
		openRelease: function() {
			wx.showActionSheet({
				itemList: ['活动', '悬赏', '招募'],
				success(res) {
					if (res.tapIndex == 0) {
						console.log('发布活动');
					} else if (res.tapIndex == 1) {
						console.log('发布悬赏');
					} else {
						console.log('发布招募');
					}
				},
				fail(res) {
					console.log(res.errMsg);
				}
			});
		}
	}
};
