import uniIcons from '../uni-icons/uni-icons.vue';
/**
 * NavBar 自定义导航栏
 * @description 导航栏组件，主要用于头部导航
 * @tutorial https://ext.dcloud.net.cn/plugin?id=52
 * @property {String} url 跳转的url地址
 * @property {String} circular 是否需要圆角样式
 * @property {String} title 标题文字
 * @property {String} leftText 左侧按钮文本
 * @property {String} rightText 右侧按钮文本
 * @property {String} leftIcon 左侧按钮图标（图标类型参考 [Icon 图标](http://ext.dcloud.net.cn/plugin?id=28) type 属性）
 * @property {String} rightIcon 右侧按钮图标（图标类型参考 [Icon 图标](http://ext.dcloud.net.cn/plugin?id=28) type 属性）
 * @property {String} color 图标和文字颜色
 * @property {String} backgroundColor 导航栏背景颜色
 * @property {Boolean} fixed = [true|false] 是否固定顶部
 * @property {Boolean} statusBar = [true|false] 是否包含状态栏
 * @property {Boolean} shadow = [true|false] 导航栏下是否有阴影
 * @event {Function} clickLeft 左侧按钮点击时触发
 * @event {Function} clickRight 右侧按钮点击时触发
 */
export default {
	name: 'UniNavBar',
	components: {
		uniIcons
	},
	props: {
		url: {
			type: String,
			default: ''
		},
		circular: {
			//是否需要圆角
			type: [Boolean, String],
			default: false
		},
		title: {
			type: String,
			default: ''
		},
		leftText: {
			type: String,
			default: ''
		},
		rightText: {
			type: String,
			default: ''
		},
		leftIcon: {
			type: String,
			default: ''
		},
		rightIcon: {
			type: String,
			default: ''
		},
		fixed: {
			type: [Boolean, String],
			default: true
		},
		color: {
			type: String,
			default: '#000000'
		},
		backgroundColor: {
			type: String,
			default: '#FFFFFF'
		},
		statusBar: {
			type: [Boolean, String],
			default: false
		},
		shadow: {
			type: [Boolean, String],
			default: false
		},
		border: {
			type: [Boolean, String],
			default: false
		}
	},
	data() {
		return {
			customBarHieight: 0 ,//设置自定义导航距离
			customTitleTop: 0 ,//设置自定义标题高度
			customBarTitle: false
		};
	},
	created() {
		this.getSystemInfo(); //获取系统信息
	},
	mounted() {
		if (uni.report && this.title !== '') {
			uni.report('title', this.title);
		}
	},
	methods: {
		onClickLeft() {
			console.log(this.$emit('clickLeft'))
			if(this.url !== "") {
				uni.redirectTo({
					url: this.url,
				})
			}else {
				console.log("url地址为空")
			}
		},
		onClickRight() {
			this.$emit('clickRight');
			if(this.url !== "") {
				uni.redirectTo({
					url: this.url,
				})
			}else {
				console.log("url地址为空")
			}
		},
		//获取系统信息
		getSystemInfo() {
			uni.getSystemInfo({
				success: e => {
					// console.log(e);
					let statusBar = 0;
					let customBar = 0;


					statusBar = e.statusBarHeight;
					customBar = e.statusBarHeight + 39;
					if (e.platform === 'android') {
						this.$store.commit('SET_SYSTEM_IOSANDROID', false);
						customBar = e.statusBarHeight + 50;
					}

					

					statusBar = e.statusBarHeight;
					// @ts-ignore
					const custom = uni.getMenuButtonBoundingClientRect(); // 获取胶囊信息
					// console.log(custom);
					if(e.model.includes("iPhone X") ){
						this.customBarTitle = custom.bottom + 'px';
					}else {
						this.customBarTitle = false;
					}
					this.customTitleTop = custom.top/2 + 'px'; 
					customBar = custom.bottom + custom.top - e.statusBarHeight + 10;


















					this.customBarHieight = customBar;

					// 这里你可以自己决定存放方式，建议放在store中，因为store是实时变化的
					this.$store.commit('SET_STATUS_BAR', statusBar);
					this.$store.commit('SET_CUSTOM_BAR', customBar);
					this.$store.commit('SET_SYSTEM_INFO', e);
				}
			});
		}
	}
};
